import { handleApi } from "@/utils/baseService";
import { callApiLayDanhMuc, callApiLayDanhSachThongBao } from "@/api/thong-bao";

const layDanhMucThongBao = (context) => {
  return new Promise((resolve, reject) => {
    handleApi(
      resolve,
      reject,
      context,
      "LAY_DANH_MUC_THONG_BAO",
      callApiLayDanhMuc()
    );
  });
};

const layDanhSachThongBao = (context, payload) => {
  return new Promise((resolve, reject) => {
    handleApi(
      resolve,
      reject,
      context,
      "LAY_DANH_SACH_THONG_BAO",
      callApiLayDanhSachThongBao(payload)
    );
  });
};

export default {
  layDanhMucThongBao,
  layDanhSachThongBao,
};
