import { handleApi } from "@/utils/baseService";
import { apiSchedule, apiDetail } from "../../../api/lich";

const getSchedule = (context, payload) => {
  return new Promise((resolve, reject) => {
    handleApi(resolve, reject, context, "GET_SCHEDULES", apiSchedule(payload));
  });
};

const getDetail = (context, id) => {
  return new Promise((resolve, reject) => {
    handleApi(resolve, reject, context, "GET_DETAIL", apiDetail(id));
  });
};

export default {
  getSchedule,
  getDetail,
};
