const LAY_DANH_SACH_NHOM_DEN_THANH_CONG = (state, danhSachNhomDen) => {
  state.danhSachNhomDen = danhSachNhomDen;
};

const TAO_NHOM_DEN_THANH_CONG = (state, NhomDen) => {
  state.danhSachNhomDen.push(
    Object.assign({}, NhomDen, { id: state.NhomDenId + 1 })
  );
};

const CAP_NHAT_NHOM_DEN_THANH_CONG = (state, NhomDen) => {
  const editItemIndex = state.danhSachNhomDen.findIndex(
    (obj) => obj.id === parseInt(NhomDen.id)
  );
  state.danhSachNhomDen[editItemIndex] = NhomDen;
};

const XOA_NHOM_DEN_THANH_CONG = (state, NhomDen) => {
  state.danhSachNhomDen.splice(state.danhSachNhomDen.indexOf(NhomDen), 1);
};

const TANG_ID_NHOM_DEN = (state) => {
  state.NhomDenId++;
};

const LAY_CHI_TIET_NHOM_DEN_THANH_CONG = (state, NhomDenId) => {
  const editItemIndex = state.danhSachNhomDen.findIndex(
    (obj) => obj.id === parseInt(NhomDenId)
  );

  state.NhomDenDangCapNhat = state.danhSachNhomDen[editItemIndex];
};

export default {
  LAY_DANH_SACH_NHOM_DEN_THANH_CONG,
  TAO_NHOM_DEN_THANH_CONG,
  CAP_NHAT_NHOM_DEN_THANH_CONG,
  XOA_NHOM_DEN_THANH_CONG,
  TANG_ID_NHOM_DEN,
  LAY_CHI_TIET_NHOM_DEN_THANH_CONG,
};
