import ENDPOINT from "@/api/endpoints";
import { HTTP } from "@/api/http-common";

export function callApiLayDanhMuc() {
  return HTTP.get(`${ENDPOINT.THONG_BAO}/categories`);
}

export function callApiLayDanhSachThongBao(payload) {
  return HTTP.get(`${ENDPOINT.THONG_BAO}/list?categoryID=${payload}`);
}
