import { handleApi } from "@/utils/baseService";
import { callApiLayDanhSachBaoCao } from "@/api/bao-cao";

const layDanhSachBaoCao = (context, payload) => {
  return new Promise((resolve, reject) => {
    handleApi(
      resolve,
      reject,
      context,
      "LAY_DANH_SACH_BAO_CAO",
      callApiLayDanhSachBaoCao(payload)
    );
  });
};

export default {
  layDanhSachBaoCao,
};
