import { ROUTE_NAME } from "@/constants/constants";
import router from "@/router";
import store from "@/store";

var refreshTokenTimeout;

function logout() {
  localStorage.removeItem("userData");
  stopRefreshTokenTimer();
  router.push({
    path: ROUTE_NAME.DANG_NHAP.NAME,
  });
}

function isAuthenticated(to, from, next) {
  if (checkAuthenticated()) {
    next();
  } else {
    next(ROUTE_NAME.DANG_NHAP.NAME);
  }
}

function notAuthenticated(to, form, next) {
  if (!checkAuthenticated()) {
    next();
  } else {
    next(ROUTE_NAME.TRANG_CHU);
  }
}

function checkAuthenticated() {
  const obj = JSON.parse(localStorage.getItem("userData"));

  if (obj && obj.access_token) {
    return true;
  }
  return false;
}

function startRefreshTokenTimer() {
  const obj = JSON.parse(localStorage.getItem("userData"));
  const expirationDuration =
    new Date(obj.expires_date).getTime() - new Date().getTime();

  refreshTokenTimeout = setTimeout(() => refreshToken(), expirationDuration);
}

function refreshToken() {
  const obj = JSON.parse(localStorage.getItem("userData"));

  if (obj) {
    const data = {
      AccessToken: obj.access_token,
      RefreshToken: obj.refresh_token,
    };
    store.dispatch("DangNhap/fn_refreshToken", data);
  }
}

function stopRefreshTokenTimer() {
  clearTimeout(refreshTokenTimeout);
}

export {
  isAuthenticated,
  checkAuthenticated,
  notAuthenticated,
  logout,
  startRefreshTokenTimer,
  refreshToken,
};
