import { handleApi } from "@/utils/baseService";
import { callApiDangNhap, refreshToken } from "@/api/dang-nhap";

const dangNhap = (context, payload) => {
  return new Promise((resolve, reject) => {
    handleApi(resolve, reject, context, "DANG_NHAP", callApiDangNhap(payload));
  });
};

const fn_refreshToken = (context, payload) => {
  return new Promise((resolve, reject) => {
    handleApi(resolve, reject, context, "REFRESH_TOKEN", refreshToken(payload));
  });
};

export default {
  dangNhap,
  fn_refreshToken,
};
