import ENDPOINT from "@/api/endpoints";
import { HTTPLAUTHEN } from "@/api/http-common";

export function callApiDangNhap(payload) {
  return HTTPLAUTHEN.post(`${ENDPOINT.DANG_NHAP}/login`, payload);
}

export function refreshToken(payload) {
  return HTTPLAUTHEN.post(`${ENDPOINT.DANG_NHAP}/refreshtoken`, payload);
}
