export function handleApi(resolve, reject, context, mutationEvent, serviceFn) {
  serviceFn
    .then((response) => {
      if (response && response.length > 1) {
        context.commit(mutationEvent, response);
        resolve(response);
      } else {
        if (response && response.status === 200) {
          context.commit(mutationEvent, response.data);
          resolve(response);
        } else {
          reject(response);
        }
      }
    })
    .catch((error) => {
      reject(error);
    });
}
