const addZero = (num) => {
  if (num < 10) {
    return `0${num}`;
  } else {
    return num;
  }
};
const getSchedules = (state) => {
  let _data = state.schedules;
  let highlightColor = {};
  if (_data) {
    _data.forEach((item) => {
      let _dateTime = new Date(`${item.date.split(" ")[0]} 00:00:00`).getTime();
      let _date = new Date(item.date);
      item.dateString = `${addZero(_date.getDate())}/${addZero(
        _date.getMonth() + 1
      )}`;
      item.timeString = `${addZero(_date.getHours())}:${addZero(
        _date.getMinutes()
      )}`;
      item.fullDateString = `${item.dateString}/${_date.getFullYear()}`;
      item.start = item.date;
      item.end = item.end_date;
      item.filterTime = _dateTime;
      if (highlightColor[item.status_color]) {
        highlightColor[item.status_color].dates.push(_date);
      } else {
        highlightColor[item.status_color] = {
          dot: {
            class: item.status_color,
          },
          dates: [_date],
        };
      }
    });
    return {
      dataNotification: _data,
      highlight: highlightColor,
    };
  }
  return null;
};
const getDetail = (state) => {
  let _detail = state.detail;
  if (_detail) {
    let _date = new Date(_detail.date);
    let _endDate = new Date(_detail.end_date);
    return {
      ..._detail,
      dateString: `${addZero(_date.getDate())}/${addZero(
        _date.getMonth() + 1
      )}/${_date.getFullYear()}`,
      startTimeString: `${addZero(_date.getHours())}:${addZero(
        _date.getMinutes()
      )}`,
      endTimeString: `${addZero(_endDate.getHours())}:${addZero(
        _endDate.getMinutes()
      )}`,
      attachments: state.attachments,
    };
  } else {
    return null;
  }
};
export default {
  getSchedules,
  getDetail,
};
