import { HTTP } from "@/api/http-common";

export function apiSchedule(payload) {
  return HTTP.get(`/meeting/list?month=${payload.month}&year=${payload.year}`);
}
export function apiDetail(id) {
  return Promise.all([
    HTTP.get(`/meeting/detail?id=${id}`),
    HTTP.get(`/meeting/detail/attachments?id=${id}`),
  ]);
}
